
.read-content {
  position: relative;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  min-height: 100%;
}
.read-content .read-content-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 40px;
    overflow: hidden;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.9);
    transform: translateY(-100%);
    transition: transform .15s;
    z-index: 99;
}
.read-content .read-content-header .back {
      position: absolute;
      top: 8px;
      left: 5px;
      text-align: center;
}
.read-content .read-content-header .back .icon {
        width: 24px;
        height: 24px;
}
.read-content .read-join-shelf {
    position: fixed;
    top: 50px;
    right: 0;
    height: 30px;
    width: 70px;
    line-height: 30px;
    text-align: center;
    border-radius: 15px 0 0 15px;
    overflow: hidden;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.9);
    transform: translateX(100%);
    transition: transform .15s;
    z-index: 99;
}
.read-content .read-content-content {
    position: relative;
    overflow: hidden;
    padding: 10px 15px 80px;
    font-size: 14px;
}
.read-content .read-content-content h4 {
      font-size: 12px;
      color: #666;
      font-weight: 400;
      position: fixed;
      top: 0px;
      left: 0px;
      right: 0;
      line-height: 30px;
      padding-left: 15px;
      z-index: 9;
}
.read-content .read-content-content .content-list h3 {
      margin: 30px 0 20px;
      font-size: 20px;
}
.read-content .read-content-content .content-list p {
      text-indent: 2em;
      margin: 0.5em 0;
      letter-spacing: 0;
      line-height: 1.8;
}
.read-content .read-content-content button.nextChapter {
      position: absolute;
      bottom: 20px;
      left: 10%;
      display: block;
      width: 80%;
      font-size: 16px;
      line-height: 36px;
      border: none;
      border-radius: 100px;
      color: #fff;
      background-color: #ed424b;
      z-index: 9999;
      outline: none;
}
.read-content .read-content-set {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 50px;
    font-size: 14px;
    overflow: hidden;
    opacity: 0;
    display: none;
    transition: opacity .15s;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.9);
}
.read-content .read-content-set .read-set-bg-list {
      display: -ms-flexbox;
      display: flex;
      padding: 10px 0;
      overflow: hidden;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.read-content .read-content-set .read-set-bg-list .read-set-bg-item {
        -ms-flex: 1;
            flex: 1;
        text-align: center;
}
.read-content .read-content-set .read-set-bg-list .read-set-bg-item span {
          position: relative;
          display: inline-block;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          border: 2px solid rgba(0, 0, 0, 0.9);
}
.read-content .read-content-set .read-set-bg-list .read-set-bg-item span .icon {
            position: absolute;
            top: 6px;
            left: 7px;
            font-size: 16px;
            color: #ed424b;
            opacity: 0;
}
.read-content .read-content-set .read-set-bg-list .read-set-bg-item span.active {
            border-color: #ed424b;
}
.read-content .read-content-set .read-set-bg-list .read-set-bg-item span.active .icon {
              opacity: 1;
}
.read-content .read-content-set .read-set-switch {
      padding: 10px 0;
      display: -ms-flexbox;
      display: flex;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.read-content .read-content-set .read-set-switch .read-set-switch-item {
        -ms-flex: 1;
            flex: 1;
}
.read-content .read-content-set .read-set-switch .read-set-switch-item span {
          display: inline-block;
          width: 80%;
          margin: 0 10%;
          border: 1px solid #fff;
          border-radius: 100px;
          line-height: 36px;
          text-align: center;
          font-size: 20px;
          color: #fff;
}
.read-content .read-content-set .read-set-switch .read-set-switch-item span.nochange {
            opacity: .6;
}
.read-content .read-content-footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;
    overflow: hidden;
    font-size: 12px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.9);
    transform: translateY(100%);
    transition: transform .15s;
    z-index: 9999;
}
.read-content .read-content-footer .footer-tab {
      position: relative;
      display: -ms-flexbox;
      display: flex;
      padding: 7px 0;
}
.read-content .read-content-footer .footer-tab-item {
      -ms-flex: 1;
          flex: 1;
      text-align: center;
}
.read-content .read-content-footer .footer-tab-item .icon {
        font-size: 20px;
}
.read-content .read-content-footer .footer-tab-item .footer-tab-label {
        margin-top: 4px;
}
.read-content .read-opt {
    transform: translate(0%, 0%);
    opacity: 1;
    display: block;
}
.read-content .read-action-mid {
    position: fixed;
    width: 60%;
    height: 40%;
    top: 30%;
    left: 20%;
    z-index: 1000;
}
.skin-default {
  background-color: #c4b395;
}
.skin-blue {
  background-color: #c3d4e6;
}
.skin-green {
  background-color: #c8e8c8;
}
.skin-pink {
  background-color: #F8C9C9;
}
.skin-dark {
  background-color: #3E4349;
}
.skin-light {
  background-color: #f6f7f9;
}
.night {
  color: rgba(255, 255, 255, 0.5);
  background-color: #1a1a1a;
}



