
.search {
  position: relative;
  overflow: hidden;
  background-color: #f6f7f9;
}
.search .search-header {
    position: relative;
    height: 40px;
    width: 100%;
    background-color: #ed424b;
}
.search .search-header .search-back {
      position: absolute;
      top: 10px;
      left: 5px;
      width: 20px;
      height: 20px;
}
.search .search-header .search-back .icon {
        font-size: 20px;
        color: #fff;
}
.search .search-header .search-input-section {
      padding: 8px 15px 7px 35px;
      overflow: hidden;
}
.search .search-header .search-input {
      width: 100%;
      height: 25px;
      color: #edb2b1;
      background-color: #ed424b;
      border: none;
      border-bottom: 1px solid #fff;
      outline: none;
}
.search .search-header ::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #edb2b1;
}
.search .search-header :-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #edb2b1;
}
.search .search-header ::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #edb2b1;
}
.search .search-header :-ms-input-placeholder {
      /* Internet Explorer 10+ */
      color: #edb2b1;
}
.search .search-header .search-icon {
      position: absolute;
      top: 10px;
      right: 15px;
      width: 20px;
      height: 20px;
}
.search .search-header .search-icon .icon {
        font-size: 20px;
        color: #fff;
}
.search .search-section {
    position: relative;
    padding: 10px 10px 20px;
    overflow: hidden;
    margin-bottom: 10px;
    background-color: #fff;
}
.search .search-section h4 {
      line-height: 30px;
      border-bottom: 1px solid #c6c7c9;
}
.search .search-section h4 span.clear-all {
        float: right;
}
.search .search-section .search-keyword-list {
      padding: 10px;
}
.search .search-section .search-keyword-list .search-keyword {
        display: inline-block;
        font-size: 12px;
        margin: 5px;
        padding: 5px;
        color: #8c8c8c;
        border: 1px solid #c6c7c9;
        border-radius: 20px;
}



