
.review {
  position: relative;
  padding-left: 15px;
  overflow: hidden;
  border-bottom: 10px solid #f6f7f9;
}
.review h2 {
    line-height: 1;
    padding-top: 10px;
    font-weight: 400;
}
.review .review-list {
    position: relative;
    overflow: hidden;
}
.review .review-item {
    position: relative;
    overflow: hidden;
    padding: 10px 0;
    font-size: 12px;
    border-bottom: 1px solid #ebebeb;
}
.review .review-item:last-child {
      border-bottom: none;
}
.review .review-item .avatar {
      position: absolute;
      top: 10px;
      left: 0;
      width: 40px;
      height: 40px;
}
.review .review-item .review-content {
      margin-left: 50px;
      padding-right: 15px;
}
.review .review-item .review-content .name {
        color: #a58d5e;
}
.review .review-item .review-content .time {
        color: #999;
}
.review .review-item .review-content .title {
        line-height: 2;
        font-weight: 700;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
}
.review .review-item .review-content .content {
        line-height: 1.5;
}
.review .review-item .review-content .like {
        margin-top: 5px;
}
.review .review-item .review-content .like .icon {
          vertical-align: -.05em;
          color: #999;
}



