
.rank {
  position: relative;
  height: 100%;
  background: #fff;
  overflow-y: scroll;
}
.rank .rank-bar-section {
    width: 80px;
    position: fixed;
    top: 40px;
    left: 0;
    bottom: 60px;
    background-color: #f6f7f9;
    overflow-y: scroll;
}
.rank .rank-bar-section .rank-list {
      width: 100%;
      overflow: hidden;
}
.rank .rank-bar-section .rank-item {
      display: block;
      width: 100%;
      height: 36px;
      line-height: 36px;
      text-align: center;
      border-left: 2px solid #f6f7f9;
      box-sizing: border-box;
}
.rank .rank-bar-section .rank-item.active {
        background-color: #fff;
        border-left: 2px solid #ed424b;
}
.rank .book-list-section {
    position: relative;
    margin: 40px 0 60px 80px;
    background-color: #fff;
}



