
.page-loading {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 99999;
  background-color: #fff;
}
.page-loading img.loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -100%);
    width: 20%;
}



