
.cat {
  position: relative;
  background: #fff;
}
.cat .cat-title {
    padding: 0 10px;
    height: 60px;
    line-height: 60px;
    font-weight: 400;
}
.cat .cat-list {
    position: relative;
    overflow: hidden;
}
.cat .cat-item {
    display: inline-block;
    width: 33.3%;
    box-sizing: border-box;
    text-align: center;
    padding: 10px 0;
}
.cat .cat-item:nth-child(3n + 1) {
      border-right: 1px solid #f0f0f0;
      border-bottom: 1px solid #f0f0f0;
}
.cat .cat-item:nth-child(3n + 2) {
      border-right: 1px solid #f0f0f0;
      border-bottom: 1px solid #f0f0f0;
}
.cat .cat-item:nth-child(3n + 3) {
      border-bottom: 1px solid #f0f0f0;
}
.cat .cat-item:nth-child(1) {
      border-top: 1px solid #f0f0f0;
}
.cat .cat-item:nth-child(2) {
      border-top: 1px solid #f0f0f0;
}
.cat .cat-item:nth-child(3) {
      border-top: 1px solid #f0f0f0;
}
.cat .cat-item p {
      line-height: 22px;
}
.cat .cat-item .cat-name {
      font-size: 16px;
      color: #333;
}
.cat .cat-item .cat-count {
      font-size: 12px;
      color: #ccc;
}



