
.book-info-section {
  position: relative;
  height: 100%;
  overflow: hidden;
  padding-top: 20px;
}
.split-line {
  padding: 0 2px;
}
.split-dot {
  padding: 0 1px;
}
.book-detail {
  overflow: hidden;
  padding: 0 15px;
}
.book-detail .book-cover {
    display: inline-block;
    width: 64px;
    height: 92px;
    margin-right: 10px;
}
.book-detail .book-desc {
    position: relative;
}
.book-detail .book-desc .book-title {
      color: #333;
      line-height: 1;
      margin: 3px 0 5px;
      font-weight: 500;
}
.book-detail .book-desc .book-meta {
      font-size: 13px;
      line-height: 1.8;
}
.book-reader {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  padding: 10px 0;
  border-top: 1px solid #ebebeb;
}
.book-reader .reader-item {
    -ms-flex: 1;
        flex: 1;
}
.book-reader .reader-item span {
      display: block;
      line-height: 1.5;
}
.book-reader .reader-item span.title {
        font-size: 12px;
        color: #999;
}
.book-reader .reader-item span.content {
        font-size: 16px;
        font-weight: 400;
        color: #222;
}
.book-intro {
  position: relative;
  padding: 10px 15px 0;
  font-size: 14px;
  line-height: 1.5;
  text-align: justify;
  border-top: 1px solid #ebebeb;
}
.book-intro .icon {
    position: absolute;
    bottom: 0;
    right: 15px;
    font-size: 16px;
    transform: rotate(270deg);
    z-index: 99;
    color: #999;
    background: rgba(255, 255, 255, 0.8);
}
.book-intro.part {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
}
.book-menu {
  position: relative;
  margin-top: 10px;
  padding: 0 15px;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  box-sizing: border-box;
  overflow: hidden;
}
.book-menu a {
    display: block;
}
.book-menu .menu-title {
    font-size: 16px;
    line-height: 36px;
    font-weight: 400;
}
.book-menu .menu-content {
    position: relative;
    font-size: 12px;
    line-height: 36px;
    width: 70%;
    text-align: right;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-right: 15px;
}
.book-menu .menu-content .icon {
      position: absolute;
      top: 10px;
      right: 0;
      font-size: 16px;
      color: #999;
      transform: rotate(180deg);
}



