
.book-bar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  background-color: #fff;
}
.book-bar .book-bar-item {
    display: block;
    -ms-flex: 1;
        flex: 1;
    padding: 10px 0;
    font-size: 12px;
    color: #999;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.book-bar .book-bar-item.read {
      font-size: 16px;
      color: #fff;
      background: #ed424b;
      line-height: 40px;
      border-top-color: #ed424b;
}
.book-bar .book-bar-icon {
    width: 24px;
    height: 24px;
    margin: 0 auto 4px;
}
.book-bar .book-bar-icon .icon {
      width: 24px;
      height: 24px;
}
.book-bar .book-bar-content {
    text-align: center;
}



