
.back-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  overflow: hidden;
  color: #fff;
  background-color: #ed424b;
  z-index: 1;
}
.back-bar .back {
    position: absolute;
    top: 8px;
    left: 5px;
    text-align: center;
}
.back-bar .back .icon {
      width: 24px;
      height: 24px;
}
.back-bar h2 {
    width: 70%;
    margin: 0 auto;
    line-height: 40px;
    text-align: center;
}



