
.dialog {
  position: relative;
}
.dialog .dialog-content {
    position: fixed;
    box-sizing: border-box;
    padding: 20px;
    width: 80%;
    min-height: 140px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    background: #fff;
    z-index: 50002;
}
.dialog .dialog-content .title {
      font-size: 16px;
      font-weight: 600;
      line-height: 30px;
}
.dialog .dialog-content .text {
      font-size: 14px;
      line-height: 30px;
      color: #555;
}
.dialog .dialog-content .btn-group {
      display: -ms-flexbox;
      display: flex;
      position: absolute;
      right: 0;
      bottom: 10px;
}
.dialog .dialog-content .btn-group .btn {
        padding: 10px 20px;
        font-size: 14px;
}
.dialog .dialog-content .btn-group .btn:last-child {
          color: #76D49B;
}
.dialog .mask {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 50001;
    background: rgba(0, 0, 0, 0.5);
}



