
.chapter {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: 9999;
  background-color: #fff;
  transform: translateX(-100%);
  transition: transform .15s;
}
.chapter.show {
    transform: translateX(0);
}
.chapter .chapter-back {
    position: relative;
    overflow: hidden;
    height: 40px;
    color: #fff;
    background-color: #ed424b;
    z-index: 1;
}
.chapter .chapter-back .back {
      position: absolute;
      top: 8px;
      left: 5px;
      text-align: center;
}
.chapter .chapter-back .back .icon {
        width: 24px;
        height: 24px;
}
.chapter .chapter-back h3 {
      width: 70%;
      margin: 0 auto;
      line-height: 40px;
      text-align: center;
      font-weight: 400;
}
.chapter .chapter-head {
    font-size: 13px;
    font-weight: 500;
    padding: 0 20px;
    line-height: 40px;
}
.chapter .chapter-head .icon {
      font-size: 13px;
}
.chapter .chapter-list-section {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    bottom: 0;
}
.chapter .chapter-bar {
    line-height: 36px;
    padding-left: 20px;
    color: #969ba3;
    background-color: #f6f7f9;
}
.chapter .chapter-list {
    position: absolute;
    top: 36px;
    right: 0;
    bottom: 0;
    left: 20px;
    overflow: auto;
}
.chapter .chapter-item {
    padding: 15px 20px 15px 0;
    border-top: 1px solid #f0f1f2;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.chapter .chapter-item.active {
      color: #ed424b;
}
.chapter .chapter-item .icon {
      font-size: 16px;
}



