
.empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.empty a {
    display: block;
    text-align: center;
    margin-top: -50px;
}
.empty .empty-icon {
    font-size: 80px;
    color: #ccc;
}
.empty .empty-notice {
    margin-top: 10px;
    color: #999;
}



