
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 40px;
  overflow: hidden;
  color: #fff;
  background-color: #ed424b;
  box-sizing: border-box;
  z-index: 99;
}
.header h2.header-logo {
    position: absolute;
    top: 12px;
    left: 15px;
    color: #fff;
    line-height: 20px;
}
.header .nav-group {
    margin: 10px auto;
    display: table;
    border: 1px solid #fff;
    border-radius: 4px;
}
.header .nav-group .nav-item {
      display: table-cell;
      font-weight: 400;
      line-height: 20px;
      padding: 0 10px;
      color: #fff;
}
.header .nav-group .nav-item.active {
        color: #666;
        background-color: #fff;
}
.header .header-search {
    position: absolute;
    display: inline-block;
    top: 12px;
    right: 15px;
}
.header .header-search .icon {
      width: 20px;
      height: 20px;
}



