
.recommend {
  position: relative;
  overflow: hidden;
}
.recommend h4 {
    font-weight: 400;
    font-size: 16px;
    color: #33373d;
    line-height: 1;
    border-left: 2px solid #ed424b;
    padding-left: 13px;
    margin: 10px 0;
}
.recommend h4 a {
      position: absolute;
      top: 12px;
      right: 15px;
      font-size: 13px;
}
.recommend h4 a::after {
        content: '>';
        display: inline-block;
        margin-left: 3px;
}
.recommend .recommend-list {
    position: relative;
    display: -ms-flexbox;
    display: flex;
}
.recommend .recommend-list .recommend-item {
      -ms-flex: 1;
          flex: 1;
      padding: 0 15px;
}
.recommend .recommend-list .cover {
      width: 60px;
      height: 80px;
}



