
.shelf-list {
  position: relative;
  overflow: hidden;
  overflow: hidden;
  padding-left: 15px;
}
.shelf-list .shelf-book-item {
    position: relative;
    padding: 10px 0;
    overflow: hidden;
    border-bottom: 1px solid #ebebeb;
}
.shelf-list .cover {
    display: inline-block;
    width: 42px;
    height: 60px;
    margin-right: 10px;
}
.shelf-list .book-info {
    position: relative;
    font-size: 12px;
    padding-right: 10px;
}
.shelf-list .book-info .title {
      font-size: 14px;
      color: #000;
      line-height: 1;
      padding: 2px 0 3px;
}
.shelf-list .book-info .title .time {
        font-weight: 400;
        margin-left: 5px;
        border: 1px solid #ed424b;
        border-radius: 8px;
        padding: 1px 4px;
        font-size: 10px;
}
.shelf-list .book-info .meta {
      line-height: 1;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      margin-right: 30px;
}
.shelf-list .book-info .meta.author {
        margin: 9px 0;
}
.shelf-list .delete {
    position: absolute;
    right: 10px;
    bottom: 10px;
}
.shelf-list .delete .icon {
      font-size: 20px;
}



