
.cat-bar {
  position: fixed;
  top: 40px;
  left: 0;
  right: 0;
  font-size: 14px;
  color: #666;
  background-color: #fff;
  z-index: 1;
}
.cat-bar .cat-filter {
    position: relative;
    border-bottom: 1px solid #e8e8e8;
    box-sizing: border-box;
}
.cat-bar .cat-fliter-list {
    position: relative;
    height: 100%;
    overflow-x: scroll;
    white-space: nowrap;
}
.cat-bar .cat-fliter-list .cat-filter-item {
      display: inline-block;
      padding: 0 5px;
      line-height: 40px;
}
.cat-bar .cat-fliter-list .cat-filter-item.active {
        color: #ed424b;
}
.cat-bar .cat-brief {
    position: relative;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #e8e8e8;
    box-sizing: border-box;
    text-align: center;
    font-size: 12px;
}
.cat-bar .cat-brief span {
      padding: 0 2px;
}
.cat-bar .cat-brief span .icon {
        width: 14px;
        height: 14px;
        margin-left: -3px;
        color: #aaa;
}



